  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

#root,
#__next {
  isolation: isolate;
}

.container {
  height: 100vh;
  display: grid;
  grid-template-rows: 100px 1fr;
  justify-items: center;
  padding: 1rem;
}

.title {
  place-self: center;
}

.game-container {
  display: grid;
  grid-template-areas:
    "message message"
    "gameboard1 gameboard2";
  grid-template-columns: auto auto;
  grid-template-rows: 50px min-content;
  gap: 50px;
}

.result-message {
  grid-area: message;
}

.gameboard1 {
  grid-area: gameboard1;
  display: grid;
  grid-template-rows: repeat(10, 50px);
  grid-template-columns: repeat(10, 50px);
  border: 1px solid black;
  background-color: yellow;
  gap: 2px;
  padding: 2px;
  position: relative;
}

.gameboard2 {
  grid-area: gameboard2;
  display: grid;
  grid-template-rows: repeat(10, 50px);
  grid-template-columns: repeat(10, 50px);
  border: 1px solid black;
  background-color: red;
  gap: 2px;
  padding: 2px;
  position: relative;
}

.square {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2em;
  border: 1px solid gray;
  background-color: white;
  cursor: pointer;
}

.missed-square {
  background-color: rgba(206, 203, 203, 0.5);
}

.attacked-square {
  background-color: blue;
}

.white-mask {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.5);
  z-index: 1;
  width: 100%;
  height: 100%;
}

.setup-page .message {
  grid-area: message;
}

.setup-page .ships-setup {
  grid-area: gameboard1;
}

.setup-page .gameboard {
  grid-area: gameboard2;
  display: grid;
  grid-template-rows: repeat(10, 50px);
  grid-template-columns: repeat(10, 50px);
  border: 1px solid black;
  background-color: red;
  gap: 2px;
  padding: 2px;
  position: relative;
}

.ships-setup {
  display: grid;
  width: 800px;
  grid-template-rows: 30px 1fr;
  gap: 1rem;
}

.ships-setup .ships {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
}

.ships-setup .ships > div {
  display: flex;
  flex-direction: column;
  align-items: start;
  min-width: 100px;
}

.ships-setup .ship, .placed-ship {
  background-color: blue;
  height: 50px;
  border-radius: 10px;
}

.ships-setup .carrier {
  width: 250px;
}

.ships-setup .battleship {
  width: 200px;
}
.ships-setup .destroyer,
.ships-setup .submarine {
  width: 150px;
}
.ships-setup .patrol-boat {
  width: 100px;
}

.ships-setup .ship.vertical {
  background-color: blue;
  width: 50px;
  border-radius: 10px;
  padding: 1px;
}

.ships-setup .carrier.vertical {
  height: 250px;
}

.ships-setup .battleship.vertical {
  height: 200px;
}
.ships-setup .destroyer.vertical,
.ships-setup .submarine.vertical {
  height: 150px;
}
.ships-setup .patrol-boat.vertical {
  height: 100px;
}

.placed-ship {
  position: absolute;
}

.buttons {
  display: flex;
  justify-content: end;
  gap: 1rem;
}`, "",{"version":3,"sources":["webpack://./src/css/style.css"],"names":[],"mappings":"AAAA;;;CAGC;;AAED;;;EAGE,sBAAsB;AACxB;;AAEA;EACE,SAAS;AACX;;AAEA;EACE;IACE,gCAAgC;EAClC;AACF;;AAEA;EACE,gBAAgB;EAChB,mCAAmC;AACrC;;AAEA;;;;;EAKE,cAAc;EACd,eAAe;AACjB;;AAEA;;;;EAIE,aAAa;AACf;;AAEA;;;;;;;EAOE,yBAAyB;AAC3B;;AAEA;EACE,iBAAiB;AACnB;AACA;;;;;;EAME,kBAAkB;AACpB;;AAEA;;EAEE,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,aAAa;EACb,6BAA6B;EAC7B,qBAAqB;EACrB,aAAa;AACf;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb;;2BAEyB;EACzB,gCAAgC;EAChC,oCAAoC;EACpC,SAAS;AACX;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,qBAAqB;EACrB,aAAa;EACb,oCAAoC;EACpC,uCAAuC;EACvC,uBAAuB;EACvB,wBAAwB;EACxB,QAAQ;EACR,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,qBAAqB;EACrB,aAAa;EACb,oCAAoC;EACpC,uCAAuC;EACvC,uBAAuB;EACvB,qBAAqB;EACrB,QAAQ;EACR,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,cAAc;EACd,sBAAsB;EACtB,uBAAuB;EACvB,eAAe;AACjB;;AAEA;EACE,0CAA0C;AAC5C;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,kBAAkB;EAClB,0CAA0C;EAC1C,UAAU;EACV,WAAW;EACX,YAAY;AACd;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,qBAAqB;EACrB,aAAa;EACb,oCAAoC;EACpC,uCAAuC;EACvC,uBAAuB;EACvB,qBAAqB;EACrB,QAAQ;EACR,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,YAAY;EACZ,4BAA4B;EAC5B,SAAS;AACX;;AAEA;EACE,aAAa;EACb,SAAS;EACT,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,sBAAsB;EACtB,YAAY;EACZ,mBAAmB;AACrB;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,YAAY;AACd;AACA;;EAEE,YAAY;AACd;AACA;EACE,YAAY;AACd;;AAEA;EACE,sBAAsB;EACtB,WAAW;EACX,mBAAmB;EACnB,YAAY;AACd;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,aAAa;AACf;AACA;;EAEE,aAAa;AACf;AACA;EACE,aAAa;AACf;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,oBAAoB;EACpB,SAAS;AACX","sourcesContent":["/*\n  Josh's Custom CSS Reset\n  https://www.joshwcomeau.com/css/custom-css-reset/\n*/\n\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\n* {\n  margin: 0;\n}\n\n@media (prefers-reduced-motion: no-preference) {\n  html {\n    interpolate-size: allow-keywords;\n  }\n}\n\nbody {\n  line-heig